import '../../App.css';
import React, { Component } from 'react';
import Footer from './footer/Footer'
import Media from './media/Media'
import Control from './functional/Control'
import Health from './functional/Health'
import Header from './header/Header'
import {Presentation} from "./presentation/Presentation";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import Preorder from "./preorder/Preorder";
import {Business} from "./functional/Business";
import {LanguagesUtils} from "../../utils/LanguagesUtils";
import strings from "../Localisation";
import Activity from "./activity/Activity";

class Total extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    async runFunction() {
        let speedMeasure = new SpeedMeasure();
        await speedMeasure.getNetworkDownloadSpeed();
        this.forceUpdate()
    }

    componentDidMount() {
        this.runFunction();
    }

    render() {
        /*
        let quality = SpeedMeasure.getResolution([
            Control.getControlArray(),
            Health.getHealthArray()
        ]);

         */

        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        return (
            <div className="app">
                <Header />
                <Presentation />
                <Control/>
                <Health/>
                <Preorder />
                <Activity />
                <Media />
                <Footer />
            </div>
        );
    }
}

export default Total;
