import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "../base/Biglinks";
import Space from "../base/functional/Space";
import { Links } from "../../utils/Links";
import {LanguagesUtils} from "../../utils/LanguagesUtils";
import strings from "../Localisation";

export class Contact extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        let links = new Links();

        return (
            <div className={"app"}>
                <Header />
                <Space />
                <Biglinks array={links.getContactArray()} />
                <Space />
                <Footer />
            </div>
        )
    }
}