import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "../base/Biglinks";
import Space from "../base/functional/Space";
import {LanguagesUtils} from "../../utils/LanguagesUtils";
import strings from "../Localisation";

export class Languages extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header />
                <Space />
                <Biglinks handle={handleStateUpdate.bind(this)} array={languages.getLanguagesArray()} />
                <Space />
                <Footer />
            </div>
        )
    }
}